/*-----------------------------------------------
9.  Wizard
-----------------------------------------------*/
.sm__wizard {
  background-color: var(--sm-wizard-bg);
  padding: calc(clamp(1rem, 0.959rem + 4.11vw, 2.5rem));
  /*box-shadow: 0px 0px 43px -16px rgba(0,0,0,0.1);*/
  min-height: calc(100vh - 14.125rem);
  display: grid;
  grid-template-rows: auto auto 1fr;
}
.sm__wizard-box {
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.sm__wizard-box, .sm__wizard-thanks {
  width: 100%;
}
.sm__wizard-header {
  box-sizing: border-box;
  background-color: var(--sm-base-white-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.sm__wizard--logo {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: calc(clamp(0.9rem, 0.471rem + 1.143vw, 1.5rem));
  border-right: 1px solid var(--sm-input-border-color);
}

.sm__wizard--logo img {
  width: calc(clamp(10rem, 15rem + 1vw, 3rem));
}

.sm_close_wizard .close_wizard_btn {
  cursor: pointer;
  padding: calc(clamp(0.9rem, 0.471rem + 1.143vw, 1.5rem));
  display: block;
  border-left: 1px solid var(--sm-input-border-color);
}
.sm_close_wizard .close_wizard_btn svg path {
  fill: var(--sm-input-focus-border-color);
}
.sm_close_wizard .close_wizard_btn:hover svg path{
  fill: var(--sm-primary-color);
}

.sm__wizard-steps {
  margin-bottom: calc(clamp(1rem, 0.973rem + 2.74vw, 2rem));
  display: flex;
  align-items: center;
  justify-content: center;
}
.sm__wizard-steps--cover {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
  width: auto;
  position: relative;
}
.sm__wizard-steps--cover::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--sm-primary-light-color);
  z-index: 1;
}
.sm__wizard-steps .sm-step {
  width: calc(clamp(1rem, 0.945rem + 5.479vw, 3rem));
  height: calc(clamp(1rem, 0.945rem + 5.479vw, 3rem));
  background-color: var(--sm-primary-light-color);
  color: var(--sm-primary-color);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(clamp(1rem, 0.997rem + 0.274vw, 1.1rem));
  line-height: 1;
  font-weight: 600;
  position: relative;
  z-index: 1;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
}
.sm__wizard-steps .sm-step-label {
  font-size: calc(clamp(0.8rem, 0.7rem + 0.274vw, 1.1rem));
  font-weight: 600;
}
.sm__wizard-steps .sm-step.active {
  background-color: var(--sm-primary-color);
  color: var(--sm-base-white-color);
  box-shadow: rgba(107, 75, 253, 0.4) 0px 0.375rem 1.25rem 0px !important;
}
.sm__wizard-category {
  text-align: center;
}
.sm__wizard-category h1, .sm__wizard-category h2 {
  margin: 0;
  padding: 0;
}
.sm__wizard-templates {
  width: 100%;
}
.sm__wizard-templates--header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: calc(clamp(1rem, 0.973rem + 2.74vw, 1.6rem));
}
.sm__wizard-templates--header .template-category-wrapper {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  flex-wrap: wrap;
}
.sm__wizard-templates--header .template-category-wrapper span {
  display: block;
  background-color: var(--sm-input-border-color);
  padding: calc(clamp(0.4rem, 0.4rem + 0vw, 0.4rem)) calc(clamp(0.8rem, 0.657rem + 0.381vw, 1rem));
  font-weight: 600;
  font-size: calc(clamp(0.8rem, 0.795rem + 0.548vw, 1rem));
  border-radius: calc(clamp(0.2rem, 0.057rem + 0.381vw, 0.4rem));
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
}
.sm__wizard-templates--header .template-category-wrapper .template-category-filter.active {
  background-color: var(--sm-primary-color);
  color: var(--sm-base-white-color);
}
.sm__wizard-templates--header .sm__wizard-templates--heading {
  margin-bottom: calc(clamp(1rem, 0.973rem + 2.74vw, 1.6rem));
}
.sm__wizard-templates--header .sm__wizard-templates--heading h2 {
  margin: 0;
  padding: 0;
  font-size: calc(clamp(1rem, 0.978rem + 2.192vw, 1.8rem));
}
.sm__wizard-cards .template_card-content {
  display: flex;
  align-items: center;
}
.sm__wizard-cards .template_card-content input {
  margin-top: 2px;
}
.sm__wizard-cards .template_card-image {
  background-color: red;
  padding-bottom: 55%;
  background-size: cover;
}

.sm_actions {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}


@media screen and (max-width: 767px) {
  .show_desktop {
    display: none !important;
  }
  .show_mobile {
    display: block !important;
  }
  .sm_mobile_filters {
    width: 100%;
  }
  .sm__wizard-templates--header {
    flex-direction: column;
  }
  .sm__wizard-templates--heading {
    text-align: center;
  }
  .select_template_category {
    max-width: 100% !important;
    width: 100%;
  }
}